import React, { Component } from 'react';
import { MenuItems } from './MenuItens';
import './navbar.css';


class Navbar extends Component {
  state = { clicked: false }
  //função que troca de estado para ativar a barra de navegação no celular
  handleClick = () => {
    this.setState({ clicked: !this.state.clicked })
  }
  render() {
    //Modificar a navbar para branco quando abaixar o scrol
    window.addEventListener('scroll', () => {
      let header = document.querySelector('header');
      header.classList.toggle('whiteNav', window.scrollY > 0);
    });
    return (
      <header>
        <div className="Navbar-Principal" >
          <h1 className="navbar-name">
            EP
          </h1>
          <div className="menu-icon" onClick={this.handleClick}>
            <i className={this.state.clicked ? 'fas fa-times' : 'fas fa-bars'}></i>
          </div>
          <ul className={this.state.clicked ? 'nav-menu active' : 'nav-menu'} id="menu">
            {MenuItems.map((items, index) => {
              return (
                <li key={index} >
                  <a className={items.cName} href={items.url}  >
                    <i className={items.cItem}></i> {items.title}
                  </a>
                </li>
              )
            })}
          </ul>
        </div>
      </header>
    );
  }
}

export default Navbar;