import './estrutura.css'

function Quadro(props) {
  return (
    <container id={props.id} className = "container-teste">
      <div className={props.itsBlack ? "quadro1" : "quadro2"} >
        <div className={props.itsBlack ? "heading1" : "heading2"}>
          <h1>{props.title}</h1>
        </div>
        <div className={props.itsBlack ? "text1" : "text2"} >
          {props.children}
        </div>
      </div>
    </container>
  );
}
export default Quadro;