import React from 'react';
import { MenuSocial } from './MenuSocialMedia.js'
import './capa.css';
function Capa(props) {
  return (
    <div className="home" id={props.id}>
      <div className = "teste">
        <img src='./FotoEfrain.jpeg' className="minha-foto" alt="Efra "></img>
        <h1 className="name" >Efrain Pantaleon</h1>
        <h2 className="work">Developer</h2>
        <ul className="menu">
          {MenuSocial.map((items, index) => {
            return (
              <li key={index}>
                <a className={items.cName} href={items.url} >
                  <i className={items.cItem}></i>
                </a>
              </li>
            )
          })}
        </ul>
      </div>
    </div>
  );
}

export default Capa;
